/* Basic Browser Reset */
html,
body {
  display: flex;
  z-index: 1;
  position: absolute;
  box-sizing: border-box;
  font-size: 16px;
  width: 100%;
  height: 100%;
  background-color: #ccc;
  background: url(/assets/images/bg-printed-circuit-boards.jpg) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

hr {
  border-color: inherit;
}

/* This has to be done, so that hr inherits the color of the main font */
#enterScriptman {
  position: relative;
  z-index: 3;
  font-family: 'Amatic SC', cursive;
  font-size: 42px;
  -webkit-text-stroke-width: 0.3px;
  -webkit-text-stroke-color: black;
  margin: auto;
  /* height: 80%;*/
  width: 80%;
  background: url(/assets/images/bg-paper2.jpg) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  /* transform: rotate(-5deg);*/
  padding: 10px 20px;
  -webkit-box-shadow: 12px 15px 15px 0px rgb(14, 8, 2);
  -moz-box-shadow: 12px 15px 15px 0px rgb(17, 10, 3);
  box-shadow: 12px 15px 15px 0px rgb(19, 11, 3);
}

.tx-mrg-10-20 {
  padding: 10px 20px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
  position: relative;
}

h1,
h2,
h3,
h4,
h5 {}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.navbar {
  border-radius: 0;
  z-index: 2;
}

.pencil {
  position: relative;
  top: 0;
  left: 0;
  z-index: 5;
  background-color: #ccc;

  height: 80%;
  width: 80%;
}